import React, {Component} from 'react';
import styled from 'styled-components';

// Create a <Title> react component that renders an <h1> which is
// centered, palevioletred and sized at 1.5em
const Title = styled.h1`
  font-size: 1.5em;
  text-align: center;
  color: palevioletred;
`;

// Create a <Wrapper> react component that renders a <section> with
// some padding and a papayawhip background
const Wrapper = styled.section`
  padding: 4em;
  background: papayawhip;
`;

class App extends Component {

    _handleClick() {
        console.log('ookokokoko')
    }

    render() {
        return (
            <html>
                <head>
                    <title>SlackChat</title>
                    <link rel="stylesheet" href="build/styles.bundle.css" />
                </head>
                <body>
                    <div className="ch-landing--hero">
                        <h2> Welcome to chindow.io </h2>
                        <button onClick={this._handleClick}>Click eeeme!</button>
                        <Wrapper>
                            <Title>Hello World, this is my first styled component!</Title>
                        </Wrapper>
                    </div>
                    <script src='build/client.bundle.js' />
                </body>
            </html>
        )
    }
}

module.exports = App


